@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.information-container {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  bottom: 0;
  right: 0;
  width: 320px;
  height: 180px;
  padding: 20px;
  margin: 20px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 0 2px rgba(40, 40, 40, 0.3);
}

.information-container .node-property-name {
  color: #afafaf;
  font-weight: 500;
}

.information-container .node-property-value {
  font-size: 1rem;
}

* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #f0f0f5;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html,
body {
  display: flex;
  flex: 1 1;
  min-height: 100%;
  max-width: 100%;
}

#root {
  display: flex;
  flex: 1 1;
}

